<template>
    <div id="editor">
        <mavon-editor style="height: 100%" v-model="value"></mavon-editor>
        <button class="btn btn-success" @click='upload_event'>{{value}}</button>
        <button class="btn btn-danger" @click='stopedit'></button>
    </div>
</template>

<script>
    // Local Registration
    import {
      mavonEditor
    } from 'mavon-editor'
    import 'mavon-editor/dist/css/index.css'
    export default {
      name: 'editor',
      data () {
        return {
          value: '',
          event_location: {}
        }
      },
      mounted: function () {
        this.event_location = this.$route.params.location
      },
      computed: {
        opt () {
          return {
            url: 'http://localhost:8080/DesktopService',
            params: {
              type: 'EVENT_UPLOAD',
              data: {
                event: this.value,
                happentime: new Date()
              }
            }
          }
        }
      },
      components: {
        mavonEditor
            // or 'mavon-editor': mavonEditor
      },
      methods: {
        upload_event () {
          if (this.location === {}) {
            alert('')
            this.$router.push('/')
          } else {
            console.log(this.event_location)
          }
        },
        stopedit () {
          if (this.$store.state.editing) {
            this.$store.commit('stopedit')
          } else {
            alert('')
          }
          this.$router.push('/')
          this.$destroy() // 
        }
      }
    }
</script>

<style>
    #editor {
        position: absolute;
        top: 100px;
        left: 10px;
        right: 10px;
        bottom: 100px;
        box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12), 0 2px 4px -1px rgba(0, 0, 0, .2);
        -webkit-box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12), 0 2px 4px -1px rgba(0, 0, 0, .2);
        z-index: 1
    }
</style>



